import React, { Component } from 'react'
import { Flex, Box } from 'reflexbox'
import NumberInput from './NumberInput'
export default class ChooseNumber extends Component {
  onClick = () => {
    this.start()
  }
  start = () => {
    const number = this.refs.numberinput.value().trim()
    if (number !== '') {
      this.props.onStart(number)
    }
  }
  randomStart = () => {
    this.props.onStart(parseInt(Math.random() * 100, 10))
  }
  render() {
    const start = this.props.start
    if (start) {
      return (
        <div>
          <img alt="?" height={150} src={require('./images/q.png')} />
        </div>
      )
    } else {
      return (
        <Flex>
          <Box w={300}>
            <NumberInput ref="numberinput" onEnter={this.start} />
          </Box>
          <Box>
            <a href="#" onClick={this.onClick}>
              <img
                alt="Start"
                src={require('./images/start.png')}
                height={150}
              />
            </a>
          </Box>
          <Box px={4}>
            <a href="#" onClick={this.randomStart}>
              <img src={require('./images/autoq.png')} alt="auto" />
            </a>
          </Box>
        </Flex>
      )
    }
  }
}



