import React, { Component } from 'react'
import Cross from '../cross.svg'

export default class Hint extends Component {
  onClose = () => {
    this.props.onClose && this.props.onClose()
  }
  render() {
    const { show } = this.props
    return show
      ? <div
          style={{
            position: 'fixed',
            bottom: 20,
            left: 20,
            right: 20,
            border: '1px solid #c0c0c0',
            background: '#f0f0f0',
            padding: 10,
            zIndex: 999
          }}
        >
          <div
            style={{
              position: 'absolute',
              right: -12,
              top: -12
            }}
          >
            <button
              style={{
                border: 'none',
                background: 'none',
                padding: 0,
                margin: 0
              }}
              onClick={this.onClose}
            >
              <img width={32} src={Cross} alt="X" />
            </button>
          </div>
          <div style={{ minHeight: 100 }}>
            {this.props.children}
          </div>
        </div>
      : null
  }
}



