import React, { Component } from 'react'
import { Flex, Box } from 'reflexbox'
import NumberGroup from './NumberGroup'

export default class Success extends Component {
  restart = () => {
    window.location.reload()
  }
  render() {
    const number = this.props.number
    const tries = this.props.tries
    const score = Math.min(100, parseInt(8 / tries * 100, 10))
    return (
      <div id="success">
        <div
          id="success_box"
          style={{
            backgroundSize: '600px auto'
          }}
        >
          <img src={require('./images/success.png')} alt="Success" />
          <div>
            <NumberGroup value={number} width={300} height={150} />
          </div>
          <Flex>
            <Box w={1 / 3}>
              用了<NumberGroup value={tries} width={90} height={45} /> 次
            </Box>
            <Box w={1 / 3}>
              <NumberGroup value={score} width={90} height={45} /> 分
            </Box>
            <Box w={1 / 3}>
              <a href="#" onClick={this.restart}>
                <img
                  src={require('./images/restart.png')}
                  alt="Restart"
                  height={60}
                />
              </a>
            </Box>
          </Flex>
        </div>
      </div>
    )
  }
}



