import React, { Component } from 'react'
import NumberGroup from './NumberGroup'
import { Flex, Box } from 'reflexbox'

export default class Record extends Component {
  render() {
    const { guess, real, isFirst } = this.props
    const img = real > guess
      ? <img
          src={require('./images/gt.png')}
          alt=">"
          height={isFirst ? 150 : 50}
        />
      : <img
          src={require('./images/lt.png')}
          alt="<"
          height={isFirst ? 150 : 50}
        />
    return (
      <Flex>
        <Box>
          <img
            height={isFirst ? 150 : 50}
            src={require('./images/q.png')}
            alt="?"
          />
          {img}
        </Box>
        <Box>
          <NumberGroup
            value={guess}
            width={isFirst ? 300 : 100}
            height={isFirst ? 150 : 50}
          />
        </Box>
      </Flex>
    )
  }
}



