import React, { Component } from 'react'
// import NumberInput from './NumberInput'
import './main.css'
import ChooseNumber from './ChooseNumber'
import Guess from './Guess'
import Record from './Record'
import Success from './Success'
import Hint from '../components/Hint'
import Info from '../components/Info'

class GuessNumber extends Component {
  state = {
    start: false,
    number: -1,
    records: [],
    success: false,
    showHint: false
  }
  onInfoClick = () => {
    this.setState({
      showHint: !this.state.showHint
    })
  }
  onCloseHint = () => {
    this.setState({
      showHint: false
    })
  }
  onStart = number => {
    if (number === '') {
      return
    }
    this.setState({
      start: true,
      number: parseInt(number, 10)
    })
  }
  onGuess = number => {
    if (parseInt(number, 10) === this.state.number) {
      this.setState({
        success: true
      })
      return
    }
    const records = [number].concat(this.state.records)
    this.setState({ records })
  }
  render() {
    return (
      <div>
        <Info onClick={this.onInfoClick} />
        <Hint show={this.state.showHint} onClose={this.onCloseHint}>
          <div>
            &nbsp;&nbsp;猜数字游戏(MonkeyGuess)由两个玩家参与。玩家一设定一个数字，不要说出来，如（8），将数字所在范围告诉玩家二，如（1~20）。玩家二开始猜数字，玩家一告诉其所猜数字和实际数字的大小关系，依次下去直到玩家二猜到玩家一设定的数字。
          </div>
          <div>
            &nbsp;&nbsp;本游戏可以允许玩家一设置数字后，玩家二进行猜测。也可以使用电脑出题，数字范围为（0~99）
          </div>
          <div>
            &nbsp;&nbsp;猜数字科学的方法是使用“二分法”：猜剩余数字区间里的中间值，如当前数字范围是（25~50)，则中间值为(25 + 50) / 2 = 38。然后根据大小信息进一步分割剩余区间。本游戏数字区间为（0~99），玩家应做到8次及以内猜出数字，超过8次将扣分。
          </div>
          <div>
            &nbsp;&nbsp;游戏目的是锻炼小朋友1. 大小比较能力；2. 100以内的加减法；3. 将100以内的数字拆成约一半的能力。
          </div>
        </Hint>
        <div>
          <img src={require('./images/title.png')} alt="MonkeyGuess" />
        </div>
        <div
          style={{
            marginLeft: 30
          }}
        >
          <ChooseNumber start={this.state.start} onStart={this.onStart} />
          {this.state.start ? <Guess onGuess={this.onGuess} /> : null}
          {this.state.records.map((n, i) => (
            <Record
              key={i}
              isFirst={i === 0}
              guess={n}
              real={this.state.number}
            />
          ))}
        </div>
        {this.state.success
          ? <Success
              number={this.state.number}
              tries={this.state.records.length + 1}
            />
          : null}
      </div>
    )
  }
}
export default GuessNumber



