import React, { Component } from 'react'
import NumberGroup from './NumberGroup'

export default class NumberInput extends Component {
  state = {
    focus: false,
    value: ''
  }
  value = () => {
    return this.state.value
  }
  onChange = e => {
    console.log(e.target.value)
    this.setState({
      focus: true,
      value: e.target.value
    })
  }
  onFocus = () => {
    this.setState({
      focus: true
    })
  }
  onBlur = () => {
    this.setState({
      focus: false
    })
  }
  onKeyDown = e => {
    if (e.key.length == 1 && (e.key < '0' || e.key > '9')) {
      e.preventDefault()
    }
  }
  onKeyUp = e => {
    if (e.key === 'Enter') {
      this.props.onEnter && this.props.onEnter()
    }
  }
  onTopClick = () => {
    this.refs.textarea.focus()
  }
  clear = () => {
    this.refs.textarea.value = ''
    this.setState({
      value: ''
    })
  }
  focus = () => {
    this.refs.textarea.focus()
  }
  render() {
    const width = this.props.width || 300
    const height = this.props.height || 150
    return (
      <div className={this.state.focus ? 'numberinput focus' : 'numberinput'}>
        <textarea
          onFocus={this.onFocus}
          onBlur={this.onBlur}
          onChange={this.onChange}
          onKeyDown={this.onKeyDown}
          onKeyUp={this.onKeyUp}
          className="numberinput"
          ref="textarea"
          maxLength={2}
        />
        <img
          alt=""
          src={require(`./images/${this.state.focus ? 'box' : 'box_blur'}.png`)}
          width={width}
          height={height}
          style={{
            position: 'absolute'
          }}
        />
        <div
          onClick={this.onTopClick}
          style={{
            position: 'absolute',
            top: '20px',
            left: '30px',
            width: width - 20 + 'px',
            height: height - 20 + 'px'
          }}
        >
          <NumberGroup
            width={240}
            height={120}
            value={this.state.value}
            cursor={true && this.state.focus}
          />
        </div>
      </div>
    )
  }
}



