import React, { Component } from 'react'

export default class NumberGroup extends Component {
  constructor() {
    super()
    this.state = {
      showCursor: false
    }
    this.timer = null
    this.cursor = false
  }
  componentDidMounted = () => {
    this.updateCursor()
  }
  componentWillReceiveProps = nextProps => {
    this.cursor = nextProps.cursor
    this.updateCursor()
  }
  updateCursor = () => {
    if (this.cursor) {
      this.setState({
        showCursor: !this.state.showCursor
      })
      if (this.timer) {
        clearTimeout(this.timer)
      }
      this.timer = setTimeout(this.updateCursor, 500)
    } else {
      this.timer = null
      this.setState({
        showCursor: false
      })
    }
  }
  render() {
    const value = this.props.value.length === 0
      ? ''
      : String(parseInt(this.props.value, 10) || 0)
    const width = this.props.width || 600
    const maxNumber = 3
    const imgWidth = parseInt(width / maxNumber, 10)
    const height = this.props.height
    const numbers = value.split('')
    return (
      <div style={{ display: 'inline-block' }}>
        {numbers.map((v, i) => (
          <img
            key={i}
            width={imgWidth}
            height={height}
            alt={v}
            src={require(`./images/${v}.png`)}
          />
        ))}
        {this.state.showCursor
          ? <img height={height} alt="|" src={require(`./images/cursor.png`)} />
          : null}
      </div>
    )
  }
}



