import React, { Component } from 'react'
import Info from '../info.svg'
export default ({ onClick }) => (
  <button
    onClick={onClick}
    style={{
      border: 'none',
      background: 'none',
      padding: 0,
      margin: 0,
      position: 'fixed',
      bottom: 10,
      right: 10,
      zIndex: 998
    }}
  >
    <img src={Info} width="32" />
  </button>
)



